@media (max-width: 1200px) {
  .carousel-content {
    left: 36%;
  }
  .carousel-title {
    font-size: 2.125rem;
  }
  .carousel-subtitle {
    font-size: 3.375rem;
  }
  .carousel-text {
    font-size: 1.125rem;
  }
}
@media (max-width: 992px) {
  .section-title {
    font-size: 2rem;
  }
  .section-subtitle {
    font-size: 1.25rem;
  }
  .carousel-content {
    left: 40%;
  }
}
@media (max-width: 768px) {
  .carousel-content {
    left: 46%;
  }
  .carousel-title {
    font-size: 1.725rem;
  }
  .carousel-subtitle {
    font-size: 2.375rem;
  }
  .carousel-text {
    font-size: 1rem;
  }
}
@media (max-width: 576px) {
  .carousel-content {
    left: 50%;
  }
  .carousel-title {
    font-size: 1.5rem;
  }
  .carousel-subtitle {
    font-size: 1.75rem;
  }
  .carousel-text {
    font-size: 1rem;
  }
  body {
    font-size: 14px;
  }
}
